// src/components/ConfigForm.tsx
import React, { useState, useCallback } from 'react';
import {
  Box,
  TextField,
  Checkbox,
  FormControlLabel,
  Typography,
  Paper,
  Divider,
  Collapse,
  IconButton,
  Grid,
} from '@mui/material';
import { ExpandMore, ExpandLess } from '@mui/icons-material';
import { StorageForm } from './StorageForm';
import { FormValues, Storage } from '../types';

interface ConfigFormProps {
  formValues: FormValues;
  onFormChange: (data: FormValues) => void;
}

const ConfigForm: React.FC<ConfigFormProps> = ({ formValues, onFormChange }) => {
  const [showAdvanced, setShowAdvanced] = useState(false);
  const [manualIPEnabled, setManualIPEnabled] = useState(!!formValues.manual_ip);

  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    onFormChange({
      ...formValues,
      [name]: type === 'checkbox' ? checked : value,
    });
  }, [formValues, onFormChange]);

  const handleMQTTInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    onFormChange({
      ...formValues,
      mqtt: {
        ...formValues.mqtt,
        [name]: type === 'checkbox' ? checked : value,
      },
    });
  }, [formValues, onFormChange]);

  const handleManualIPChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    onFormChange({
      ...formValues,
      manual_ip: {
        ...formValues.manual_ip,
        [name]: value,
      },
    });
  }, [formValues, onFormChange]);

  const handleStorageChange = useCallback((storages: Storage[]) => {
    onFormChange({
      ...formValues,
      storages,
    });
  }, [formValues, onFormChange]);

  const handleManualIPToggle = () => {
    setManualIPEnabled((prev) => !prev);
    onFormChange({
      ...formValues,
      manual_ip: !manualIPEnabled ? { ip: '', gateway: '', subnet: '', dns: '' } : undefined,
    });
  };

  return (
    <Paper elevation={3} sx={{ padding: 2 }}>
      <Typography variant="h6">General Settings</Typography>
      <TextField
        label="Name"
        name="name"
        value={formValues.name}
        onChange={handleInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Friendly Name"
        name="friendly_name"
        value={formValues.friendly_name}
        onChange={handleInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Board"
        name="board"
        value={formValues.board}
        onChange={handleInputChange}
        fullWidth
        margin="normal"
      />

      <Divider sx={{ my: 2 }} />

      <Typography variant="h6">MQTT Settings</Typography>
      <TextField
        label="Topic"
        name="topic"
        value={formValues.mqtt.topic}
        onChange={handleMQTTInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Broker"
        name="broker"
        value={formValues.mqtt.broker}
        onChange={handleMQTTInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Port"
        name="port"
        type="number"
        value={formValues.mqtt.port}
        onChange={handleMQTTInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Username"
        name="username"
        value={formValues.mqtt.username}
        onChange={handleMQTTInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Password"
        name="password"
        type="password"
        value={formValues.mqtt.password}
        onChange={handleMQTTInputChange}
        fullWidth
        margin="normal"
      />
      <FormControlLabel
        control={
          <Checkbox
            checked={formValues.mqtt.discovery}
            onChange={handleMQTTInputChange}
            name="discovery"
          />
        }
        label="Discovery"
      />

      <Divider sx={{ my: 2 }} />

      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Typography variant="h6">Advanced Settings</Typography>
        <IconButton onClick={() => setShowAdvanced(!showAdvanced)}>
          {showAdvanced ? <ExpandLess /> : <ExpandMore />}
        </IconButton>
      </Box>
      <Collapse in={showAdvanced}>
        <TextField
          label="Restart after error count"
          name="restart_after_error_count"
          type="number"
          value={formValues.restart_after_error_count}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={formValues.enable_cellquery}
              onChange={handleInputChange}
              name="enable_cellquery"
            />
          }
          label="Enable Cellquery"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={formValues.enable_cmd30}
              onChange={handleInputChange}
              name="enable_cmd30"
            />
          }
          label="Enable CMD30"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={formValues.enable_esp_temperature}
              onChange={handleInputChange}
              name="enable_esp_temperature"
            />
          }
          label="Enable ESP temperature"
        />
        <FormControlLabel
          control={
            <Checkbox
              checked={formValues.enable_powermeter}
              onChange={handleInputChange}
              name="enable_powermeter"
            />
          }
          label="Enable Powermeter"
        />

        <FormControlLabel
          control={
            <Checkbox checked={manualIPEnabled} onChange={handleManualIPToggle} />
          }
          label="Manual IP"
        />

        {manualIPEnabled && (
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                label="IP Address"
                name="ip"
                value={formValues.manual_ip?.ip || ''}
                onChange={handleManualIPChange}
                fullWidth
                margin="normal"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="Gateway"
                name="gateway"
                value={formValues.manual_ip?.gateway || ''}
                onChange={handleManualIPChange}
                fullWidth
                margin="normal"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="Subnet"
                name="subnet"
                value={formValues.manual_ip?.subnet || ''}
                onChange={handleManualIPChange}
                fullWidth
                margin="normal"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label="DNS"
                name="dns"
                value={formValues.manual_ip?.dns || ''}
                onChange={handleManualIPChange}
                fullWidth
                margin="normal"
              />
            </Grid>
          </Grid>
        )}
      </Collapse>

      <Divider sx={{ my: 2 }} />

      <StorageForm
        storages={formValues.storages}
        onChange={handleStorageChange}
        maxStorages={3}
      />
    </Paper>
  );
};

export default ConfigForm;
