// src/components/StorageForm.tsx
import React from 'react';
import { Button, TextField, Box, Typography } from '@mui/material';
import { Storage } from '../types';

interface StorageFormProps {
  storages: Storage[];
  onChange: (storages: Storage[]) => void;
  maxStorages: number;
}

const StorageForm: React.FC<StorageFormProps> = ({ storages, onChange, maxStorages }) => {
  const handleStorageChange = (index: number, key: string, value: string | number) => {
    const newStorages = [...storages];
    newStorages[index] = { ...newStorages[index], [key]: value };
    onChange(newStorages);
  };

  const handleAddStorage = () => {
    if (storages.length < maxStorages) {
      onChange([...storages, { version: 1, mac_address: '!secret storage_mac' }]);
    }
  };

  const handleRemoveStorage = (index: number) => {
    const newStorages = storages.filter((_, i) => i !== index);
    onChange(newStorages);
  };

  return (
    <Box mt={2}>
      <Typography variant="h6">Storages</Typography>
      {storages.map((storage, index) => (
        <Box key={index} mb={2} border={1} borderRadius={5} padding={2}>
          <TextField
            label="Version"
            type="number"
            value={storage.version}
            onChange={(e) => handleStorageChange(index, 'version', parseInt(e.target.value, 10))}
            fullWidth
            margin="normal"
            inputProps={{ min: 1, max: 2 }}
          />
          <TextField
            label="MAC Address"
            value={storage.mac_address}
            onChange={(e) => handleStorageChange(index, 'mac_address', e.target.value)}
            fullWidth
            margin="normal"
          />
          <Button variant="contained" color="secondary" onClick={() => handleRemoveStorage(index)}>
            Remove
          </Button>
        </Box>
      ))}
      {storages.length < maxStorages && (
        <Button variant="contained" color="primary" onClick={handleAddStorage}>
          Add Storage
        </Button>
      )}
    </Box>
  );
};

export { StorageForm };
